<template>
  <div>
    <navMedic/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Datos del Médico</b>
      </h1>
      <h3 class="text-center text-dark">Realice los cambios que desea modificar</h3>
      <div style="width:400px">
        <b-alert show variant="danger" style="width:400px" v-if="isCorrect!==null">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
      </div>
      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-4">
            <label for="tipoDocumento">Tipo de Documento</label>
            <select
              class="custom-select"
              id="tipoDocumento"
              v-model.number="tipoDocumento.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Pasaporte</option>
              <option value="2">Cedula</option>
            </select>
          </div>
          <div class="col-md-4">
            <label for="numDocumento">Numero de Documento</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              maxlength="10"
              v-model="medic.numDocumento"
              type="text"
              required
            >
          </div>
          <div class="col-md-4">
            <label for="numDocumento">Numero de Matricula</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              maxlength="10"
              v-model="medic.numMatricula"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="nombre">Nombres</label>
            <input
              class="form-control"
              placeholder="Ingrese nombres"
              v-model="medic.nombre"
              type="text"
              required
            >
          </div>
          <div class="col-md-6">
            <label for="nombre">Apellidos</label>
            <input
              class="form-control"
              placeholder="Ingrese apellidos"
              v-model="medic.apellido"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="numTelefono">Numero Telefono</label>
            <input
              class="form-control"
              placeholder="Numero de telefono"
              v-model="medic.numTelefono"
              type="text"
            >
          </div>
          <div class="col-md-6">
            <label for="numTelefono">Numero Celular</label>
            <input
              class="form-control"
              placeholder="Numero de celular"
              v-model="medic.numCelular"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-12">
            <label for="especialidad">Especialidad</label>
            <input
              class="form-control"
              placeholder="Especialidad"
              v-model="medic.especialidad"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <button class="btn btn-primary">Enviar</button>
        </div>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navMedic from "@/components/medic/navMedic.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "editarMedico",
  data() {
    return {
      id: this.$route.params.id,
      medic: [],
      message: null,
      isCorrect: null,
      avise: null,
      tipoDocumento: {
        id: null
      }
    };
  },
  methods: {
    async getMedicById() {
      axios
        .get(`${URL}/api/medico/${this.id}`)
        .then(response => {
          this.tipoDocumento = response.data.tipoDocumento;
          this.medic = response.data;
        })
        .catch(error => {
          bootbox.alert({
            title: "Error",
            message: error.response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/medicos")
          });
          console.log("Error " + error.response.status + " Not Found");
        });
    },
    save(e) {
      e.preventDefault();
      this.avise = "";
      this.message = "";
      axios
        .put(`${URL}/api/medico/${this.id}`, {
          tipoDocumento: { id: this.tipoDocumento.id },
          numMatricula: this.medic.numMatricula,
          apellido: this.medic.apellido,
          nombre: this.medic.nombre,
          numDocumento: this.medic.numDocumento,
          especialidad: this.medic.especialidad,
          numTelefono: this.medic.numTelefono,
          numCelular: this.medic.numCelular
        })
        .then(response => {
          bootbox.alert({
            title: "Datos Actualizados",
            message: response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/medicos")
          });
        })
        .catch(error => {
          this.avise = "Error";
          this.isCorrect = false;
          this.message = "Detalle: " + error.response.data.mensaje;
          bootbox.alert({
            title: "Error",
            message: "No se pudo editar",
            locale: "es",
            backdrop: true
          });
          console.log("Error " + error.response.status + " Conflict");
        });
    }
  },
  components: {
    navMedic,
    Footer
  },
  mounted() {
    this.getMedicById();
  }
};
</script>

